@font-face {
  font-family: "PTSans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "PTSans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  font-family: "PTSans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
}
.container {
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}
.page-header {
  margin-bottom: 56px;
}
.page-header__wrapper {
  display: flex;
  justify-content: center;
}
.page-header__link {
  width: 102px;
  height: 83px;
  z-index: 1;
  position: absolute;
}
@media (min-width: 768px) {
  .page-header {
    margin: 0;
  }
  .page-header__link {
    position: absolute;
    top: 0;
  }
}
@media (min-width: 1200px) {
  .page-header__link {
    width: 188px;
    height: 96px;
  }
}
.nav {
  position: relative;
}
.nav__list {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin: 0;
}
.nav__item {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  background-color: #81b3d2;
  list-style: none;
  text-align: center;
}
.nav__item:hover {
  background-color: #669ec0;
}
.nav__item:active {
  background-color: #5496bd;
}
.nav__item:disabled {
  background-color: #e5e5e5;
}
.nav__item:last-child {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.nav__link {
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  padding: 18px 0;
  display: block;
}
.nav__link:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .nav__toggle {
    position: absolute;
    width: 23px;
    height: 21px;
    cursor: pointer;
    border: none;
    top: 18px;
    right: 21px;
    padding: 0;
  }
  .nav__toggle:hover {
    opacity: 0.6;
  }
  .nav__toggle:active {
    opacity: 0.3;
  }
  .nav--closed .nav__toggle::before {
    content: "";
    position: absolute;
    width: 23px;
    height: 4px;
    background-color: #81b3d2;
    box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
    top: 0;
    left: 0;
  }
  .nav--opened .nav__toggle {
    top: 17px;
    right: 21px;
    background-color: transparent;
  }
  .nav--opened .nav__toggle::before,
  .nav--opened .nav__toggle::after {
    content: "";
    position: absolute;
    cursor: pointer;
    width: 27px;
    height: 4px;
    background-color: #ffffff;
    top: 9px;
    left: -1px;
  }
  .nav--opened .nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .nav--opened .nav__toggle::after {
    transform: rotate(-45deg);
  }
  .nav--opened .nav__toggle:hover::before,
  .nav--opened .nav__toggle:hover::after {
    background-color: rgba(255, 255, 255, 0.6);
  }
  .nav--opened .nav__toggle:active::before,
  .nav--opened .nav__toggle:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
  .nav--closed .nav__link {
    display: none;
  }
  .nav--nojs .nav__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .nav__list {
    flex-direction: row;
    justify-content: space-between;
  }
  .nav__item {
    background-color: #ffffff;
    width: 153px;
  }
  .nav__item:hover,
  .nav__item:active {
    background-color: #ffffff;
  }
  .nav__item:nth-child(2) {
    margin-right: auto;
  }
  .nav__link {
    color: #000000;
    font-size: 14px;
    line-height: 26px;
    padding: 16px 0;
  }
  .nav__link:hover {
    color: #81b3d2;
  }
  .nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .nav__item {
    width: 243px;
  }
}
